import {
  useHistory
} from "react-router-dom";
import * as backend from '../build/index.main.mjs'
import Button from 'react-bootstrap/Button'
import { useState } from "react";
import { Col, Row } from "react-bootstrap";

const Sell = (props) => {
  let history = useHistory();
  const { acc, stdlib } = props
  const [state, setState] = useState({
    success: false
  })
  const [query, setQuery] = useState({})
  const handleSubmit = async () => {
    /*
    setState({...state, success: true})
    return
    */
    if (!query.ASSETID
      || !query.STARTBID
      || !query.DEADLINEDATE) {
      alert("Enter Asset id, Start bid, and Deadline date to submit")
      return
    }
    console.log("Launching new game ...")
    const ctcAuction = acc.deploy(backend)
    const ctcAuctionInfo = await ctcAuction.getInfo()
    await Promise.all([
      backend.Auctioneer(ctcAuction, {
        getParams: () => new Promise((resolve) => {
          resolve({
            addr: '7GDBWS6G5YB5TS4SYTY5IRKHYR5G7JZIAAIGNNSV3VFQJ7MOA3EZZ3QLFI',
            token: parseInt(query.ASSETID), //24270812, // ShitCoin
            reservePrice: stdlib.parseCurrency(100), // 15 ALGO
            deadline: 100, // x blocks
            maxDeadline: 100, // y blocks, where y > x
            unitAmount: stdlib.parseCurrency(1),
            deadlineStep: 100
          })
        }),
        signal: () => setState({ ...state, success: true, appId: ctcAuctionInfo }),
        ...stdlib.hasConsoleLogger,
      }),
    ])
  }
  const handleChange = ({ target }) => {
    const { name, value } = target
    setQuery({
      ...query,
      [name]: value
    })
  }
  /*
  const handleSubmit = () => {
    if (query.ASSETID
      && query.STARTBID
      && query.DEADLINEDATE)
      setState({
        ...state,
        success: true
      })
    else
      alert("Enter Asset id, Start bid, and Deadline date to submit")
  }
  */
  const handleSellOneMore = () => {
    setState({
      success: false
    })
    setQuery({})
  }
  const serviceNameTypographyStyle =
  {
    "fontFamily": "Rubik",
    "fontStyle": "normal",
    "fontWeight": "900",
    "fontSize": "32px",
    "lineHeight": "38px",
    "textAlign": "center",
    "letterSpacing": "0.1em",
    "textTransform": "uppercase"
  }
  const cardStyle =
  {
    /*
    "position": "fixed",
    "top": "15%",
    "left": "25%",
    */
    "background": "#FFFFFF",
    "borderRadius": "36px",
    "padding": "50px 56px",
    "marginTop": "5vh",
    //"maxWidth": "90vw",
    //"maxHeight": "70vh",
    "width": "465px",
    "height": "545px"
  }
  const compactCardStyle =
  {
    "position": "fixed",
    "top": "15%",
    "left": "5vw",
    "width": "90vw",
    "background": "#FFFFFF",
    "borderRadius": "36px",
    "padding": "20px 30px",
    "marginTop": "0vh",
    //"maxWidth": "90vw",
    //"maxHeight": "70vh",
    //"width": "465px",
    //"height": "588px"
  }

  const Success = () => {
    const cardStyle =
    {
      "marginTop": "5vh",
      "width": "391px",
      "background": "#FFFFFF",
      "borderRadius": "36px",
      "padding": "44px"
    }
    const compactCardStyle =
    {
      "marginTop": "5vh",
      "width": "90vw",
      "background": "#FFFFFF",
      "borderRadius": "36px",
      "padding": "44px"
    }
    const imageStyle = {
      "marginTop": "26px"
    }
    const labelStyle = {
      "fontFamily": "Rubik",
      "fontStyle": "normal",
      "fontWeight": "300",
      "fontSize": "12px",
      "lineHeight": "14px",
      "color": "#C558F3"
    }
    const valueStyle =
    {
      "fontFamily": "Rubik",
      "fontStyle": "normal",
      "fontWeight": "500",
      "fontSize": "14px",
      "lineHeight": "17px",
      "color": "#55595D",
      "marginTop": "5px"
    }
    const buttonContainerStyle =
    {
      "display": "flex",
      "justifyContent": "center"
    }
    const buttonStyle =
    {
      "width": "100%",
      "maxWidth": "304px",
      "height": "50px",
      "lineHeight": "50px",
      "background": "linear-gradient(111.85deg, #FB87FF -23.82%, #AE44ED 119.4%)",
      "boxShadow": "0px 10px 20px rgba(219, 134, 255, 0.66)",
      "borderRadius": "44px",
      "marginTop": "55px",
      "color": "#ffffff"
    }
    const sellMoreStyle =
    {
      "height": "17px",
      "fontFamily": "Rubik",
      "fontStyle": "normal",
      "fontWeight": "normal",
      "fontSize": "14px",
      "lineHeight": "17px",
      "textAlign": "center",
      "textTransform": "uppercase",
      "color": "#C054F2",
      "marginTop": "32px"
    }
    const Template = (props) => {

      return <>
        <div style={serviceNameTypographyStyle}>Success</div>
        <div>
          <img style={imageStyle} src="https://via.placeholder.com/254" alt="NFT" />
          <Row style={{ marginTop: '34px' }}>
            {[
              {
                label: "Asset id",
                value: query.ASSETID,
                displayValue: (val) => `${val}`
              },
              {
                label: "Start bid",
                value: query.STARTBID,
                displayValue: (val) => `${val} ALGO`
              },
              {
                label: "Deadline date",
                value: query.DEADLINEDATE,
                displayValue: (val) => `${val}`
              }
            ].map(el => <Col style={{ padding: '0px', height: '38px', overflow: 'hidden' }}>
              <Row>
                <Col xs={12} className="text-left" style={labelStyle}>{el.label}</Col>
                <Col xs={12} style={valueStyle}>{el.value ? el.displayValue(el.value) : '-'}</Col>
              </Row>
            </Col>)}
          </Row>
          <div style={buttonContainerStyle}>
            <div role="button" style={buttonStyle} onClick={() => history.push(`/auction/${state.appId}`)}>OK</div>
          </div>
          <div role="button" style={sellMoreStyle} onClick={handleSellOneMore}>Sell One More NFT</div>
        </div>
      </>
    }
    return <>
    <div className="d-none d-sm-inline-block" style={cardStyle}>
      <Template />
    </div>
    <div className="d-xs-inline-block d-sm-none" style={compactCardStyle}>
      <Template />
    </div>
    </>
  }
  const inputStyle =
  {
    "height": "48px",
    "background": "#FCFCFC",
    "border": "1px solid #F2F2F2",
    "boxSizing": "border-box",
    "borderRadius": "9px",
    "width": "100%",
    "display": "block"
  }
  const labelTypographyStyle =
  {
    "fontFamily": "Rubik",
    "fontStyle": "normal",
    "fontWeight": "300",
    "fontSize": "12px",
    "lineHeight": "14px",
    "color": "#C558F3"
  }
  const labelStyle =
  {
    "height": "14px",
    "marginBottom": "4px"
  }
  const buttonStyle =
  {
    "height": "50px",
    "background": "linear-gradient(111.85deg, #FB87FF -23.82%, #AE44ED 119.4%)",
    "boxShadow": "0px 10px 20px rgba(219, 134, 255, 0.66)",
    "borderRadius": "44px",
    "marginTop": "46px"
  }
  const buttonTypographyStyle =
  {
    "fontFamily": "Rubik",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "14px",
    "lineHeight": "50px",
    "textAlign": "center",
    "textTransform": "uppercase",
    "color": "#FFFFFF"
  }
  const cancelStyle =
  {
    "fontFamily": "Rubik",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "14px",
    "lineHeight": "17px",
    "textAlign": "center",
    "textTransform": "uppercase",
    "color": "#FF4747",
    "marginTop": "32px"
  }
  return <div id="sell" style={{ paddingBottom: "100px" }}>
    {!state.success ? <>
      <div className="d-xs-block d-sm-none" style={{ ...compactCardStyle }} >
        <div style={{ ...serviceNameTypographyStyle, ...{ "fontSize": "22px" } }}>Sell Your NFT</div>
        <div className="jam default" style={{ margin: 'auto' }}>
          {[
            {
              name: "ASSETID",
              label: "Asset id",
              placeholder: "Enter 7 digits number. Example : 9381029",
              style: { textAlign: 'left' }
            },
            {
              name: "STARTBID",
              label: "Start bid",
              placeholder: "Enter price in Algorand",
              style: { marginTop: "14px", textAlign: 'left' }
            },
            {
              name: "DEADLINEDATE",
              label: "Deadline date",
              placeholder: "Select date when the auction will be finished",
              style: { marginTop: "14px", textAlign: 'left' }
            },
          ].map((el, i) => <div key={`sell-input-${i}`} style={el.style}>
            <label style={{ ...labelStyle, ...labelTypographyStyle }}>{el.label}</label>
            <input style={inputStyle} name={el.name} defaultValue={query[el.name]} placeholder={el.placeholder} onChange={handleChange} />
          </div>)}
          <div role="button" style={{ ...buttonStyle, ...buttonTypographyStyle, ...{ "marginTop": "40px" } }} onClick={handleSubmit}>Submit</div>
          <div className="btn-cancel" style={cancelStyle} onClick={() => history.push("/")}>Cancel</div>
        </div>
      </div>
      <div id="sell" className="d-none d-sm-block" style={cardStyle}>
        <div style={serviceNameTypographyStyle}>Sell Your NFT</div>
        <div className="jam default" style={{ margin: '24px auto' }}>
          {[
            {
              name: "ASSETID",
              label: "Asset id",
              placeholder: "Enter 7 digits number. Example : 9381029",
              style: { textAlign: 'left' }
            },
            {
              name: "STARTBID",
              label: "Start bid",
              placeholder: "Enter price in Algorand",
              style: { marginTop: "14px", textAlign: 'left' }
            },
            {
              name: "DEADLINEDATE",
              label: "Deadline date",
              placeholder: "Select date when the auction will be finished",
              style: { marginTop: "14px", textAlign: 'left' }
            },
          ].map((el, i) => <div key={`sell-input-${i}`} style={el.style}>
            <label style={{ ...labelStyle, ...labelTypographyStyle }}>{el.label}</label>
            <input style={inputStyle} name={el.name} defaultValue={query[el.name]} placeholder={el.placeholder} onChange={handleChange} />
          </div>)}
          <div role="button" style={{ ...buttonStyle, ...buttonTypographyStyle }} onClick={handleSubmit}>Submit</div>
          <div role="button" style={cancelStyle} onClick={() => history.push("/")}>Cancel</div>
        </div>
      </div>
    </> : <Success />}
  </div>
}

export default Sell;